<script>
import balekLogo from '../balek/resources/Balek.svg'
import digivigilLogo from '../digivigil/resources/digivigil.svg'
import diaplodeLogo from '../diaplode/resources/Diaplode.svg'

export default {
  name: "MainHeader",
  data() {
    return {
      balekLogo: balekLogo,
      digivigilLogo: digivigilLogo,
      diaplodeLogo: diaplodeLogo,
      windowWidth: window.innerWidth
    }
  },
  computed: {

      showHeaderLinkTitle(){
        return this.windowWidth >= 720;
      }

  },
  created() {
    window.addEventListener('resize', this.onResize)
  },
  destroyed() {
    window.removeEventListener('resize', this.onResize)
  },
  methods: {
    onResize() {
    this.windowWidth = window.innerWidth
    }
  }

};
</script>
