 >= 720;\n      }\n\n  },\n  created() {\n    window.addEventListener('resize', this.onResize)\n  },\n  destroyed() {\n    window.removeEventListener('resize', this.onResize)\n  },\n  methods: {\n    onResize() {\n    this.windowWidth = window.innerWidth\n    }\n  }\n\n};\n</script>\n<style>\n\n.MenuHeaderMainDiv{\n  position: relative;\n  z-index: 10;\n  text-align: center;\n  margin-bottom: 10px;\n  background: rgba(0, 0, 0, .3);\n\n}\n\n\n.MainHeaderSpan{\n  display: inline-block;\n  vertical-align: middle;\n\n  white-space: nowrap;\n}\n\n.MainHeaderRouterLink{\n  vertical-align: top;\n  display: inline-block;\n  text-decoration: none;\n  color: #4796c5;\n}\n\n\n.MainHeaderRouterLink:hover{\n filter: invert();\n}\n\n.HeaderSelectionImage{\n  display: inline-block;\n  vertical-align: middle;\n  width: 25px;\n  height: 25px;\n  margin: 0;\n  padding: 0;\n  line-height:20px;\n}\n.MainHeaderSelectionDiv{\n  display: inline-block;\n  font-size: 14pt;\n  margin: 10px;\n  border-radius: 15px;\n  background: rgba(255, 255, 255, 0.9);\n  color: #0c2a3b;\n  border: solid black 3px;\n  padding: 5px;\n  padding-left: 10px;\n  padding-right: 10px;\n\n  text-decoration: none;\n\n  white-space: nowrap;\n\n}\n\n.MainInfoDiv{\n  padding: 3px;\n  padding-top: 10px;\n  font-size: 18pt;\n  position: relative;\n  vertical-align: middle;\n  margin: 0;\n  text-align: left;\n  display: block;\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n}\n\n\n.MainYouTubeDiv{\n\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n\n}\n\n\n</style>"],"sourceRoot":""}]);
